@media (max-width: 867px) {
  .features__list {
    flex-direction: column;
    column-gap: 0;
  }
}
.features__list-item {
  width: calc(50% - 25px);
  height: 200px;
}
@media (max-width: 867px) {
  .features__list-item {
    width: 100%;
    height: fit-content;
    min-height: 200px;
  }
}

.methodology__list {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-gap: 20px;
}
@media (max-width: 1199px) {
  .methodology__list {
    grid-template-columns: 1fr;
    max-width: 600px;
    margin: 0 auto;
  }
}

.result-slider {
  max-width: 820px;
  margin: 0 auto;
}
.result-slider-controls {
  align-items: center;
  justify-content: flex-end;
  padding: 20px 0;
}
@media (max-width: 533px) {
  .result-slider-controls {
    justify-content: center;
  }
}
.result-slider-button-next {
  margin-right: 0;
}
.result-slider .swiper-slide {
  position: relative;
}
.result-slider .swiper-slide::before, .result-slider .swiper-slide::after {
  position: absolute;
  bottom: 0;
  width: fit-content;
  height: fit-content;
  padding: 5px;
  letter-spacing: 6px;
  font-size: 12px;
  border-radius: 0px 10px 0px 0px;
  color: var(--warning);
  background-color: var(--white);
}
.result-slider .swiper-slide::before {
  left: 0;
  content: "До";
}
.result-slider .swiper-slide::after {
  left: 50%;
  content: "После";
}
@media (max-width: 533px) {
  .result-slider img {
    width: 100%;
  }
}

.staff__list {
  display: flex;
  justify-content: space-between;
  max-width: 700px;
  margin: 0 auto;
}
@media (max-width: 620px) {
  .staff__list {
    flex-direction: column;
    gap: 30px;
  }
}
.staff__list-item {
  align-items: center;
}

.consult__list {
  max-width: 860px;
  margin: 0 auto;
}
.consult__list-item {
  display: flex;
  margin-bottom: 20px;
}
.consult__list-item-num {
  position: relative;
  display: block;
  font-size: 64px;
  line-height: 1;
  font-weight: 700;
  color: var(--white);
  background-color: var(--white);
  text-shadow: -1px -1px 0 var(--warning), 1px -1px 0 var(--warning), -1px 1px 0 var(--warning), 1px 1px 0 var(--warning);
  padding-right: 20px;
  margin-right: 16px;
  width: fit-content;
  height: fit-content;
}
@media (max-width: 867px) {
  .consult__list-item-num {
    font-size: 40px;
    padding-right: 10px;
  }
}
.consult__list-item-num::after {
  position: absolute;
  content: "";
  bottom: 8px;
  right: 0;
  width: 16px;
  height: 16px;
  border: 1px solid var(--warning);
  border-radius: 50%;
}
@media (max-width: 867px) {
  .consult__list-item-num::after {
    width: 8px;
    height: 8px;
    bottom: 5px;
  }
}
.consult__list-item-text {
  color: var(--darkgray);
  font-size: 20px;
  letter-spacing: -0.4px;
  padding-top: 7px;
  max-width: 790px;
}
@media (max-width: 867px) {
  .consult__list-item-text {
    font-size: 16px;
    padding-top: 4px;
  }
}
.consult__list-item-text strong {
  font-weight: 700;
}

.reviews .swiper-container {
  position: relative;
}
.reviews__link {
  max-width: 150px;
  font-weight: 700;
  margin: 0 auto;
  background-color: var(--warning);
  color: var(--white);
}
.reviews-slider {
  margin-bottom: 50px;
  max-width: 1080px;
}
.reviews-slider .swiper-slide {
  box-sizing: border-box;
  max-width: 350px;
  height: auto;
}
.reviews-slider-button-next, .reviews-slider-button-prev {
  position: absolute;
  top: 50%;
  margin: 0;
  background-color: var(--white);
}
@media (max-width: 533px) {
  .reviews-slider-button-next, .reviews-slider-button-prev {
    display: none;
  }
}
.reviews-slider-button-next {
  right: 10px;
  transform: translateY(-50%);
}
.reviews-slider-button-prev {
  left: 10px;
  transform: translateY(-50%) rotate(180deg);
}

/* Модальные окна */